body {
  background: #fff;
  font-family: Montserrat, sans-serif;
  font-weight: 500;
}

.noscroll {
  position: fixed;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 1280px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 100%;
    max-width: max-content;
  }
}

header {
  margin: 0;
  padding: 20px 0;
}

.header-wrap {
  text-align: left;
  flex-direction: row;
  justify-content: center;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-wrap {
    flex-flow: column wrap;
  }
}

.header-logo {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-logo img {
  max-width: none;
}

.header-logo p {
  color: #424242;
  text-shadow: 0 2px 2px #efefef;
  padding: 20px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.15;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-logo {
    text-align: center;
    flex-direction: column;
    justify-content: center;
  }

  .header-logo p {
    padding: 20px 0;
    font-size: 16px;
  }

  .header-logo img {
    max-width: 100%;
    width: 150px;
    height: auto;
  }
}

.header-map {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-map img {
  max-width: none;
}

.header-map p {
  padding: 20px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.2;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-map {
    justify-content: center;
  }

  .header-map img {
    display: none;
  }

  .header-map p {
    text-align: center;
    padding: 20px 0;
  }
}

.header-contacts {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-contacts img {
  max-width: none;
}

.header-contacts p {
  flex-wrap: wrap;
  padding: 20px;
  display: flex;
}

.header-contacts a:nth-child(1) {
  color: #424242;
  font-weight: 700;
  text-decoration: none;
}

.header-contacts a:nth-child(2) {
  color: #126c03;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-contacts {
    justify-content: center;
  }

  .header-contacts img {
    width: 70px;
    padding-right: 20px;
  }

  .header-contacts p {
    flex-flow: column wrap;
    padding: 20px 0;
    display: flex;
  }
}

.header-menu ul {
  width: 1280px;
  height: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-menu ul {
    display: none;
  }
}

.header-menu ul, .header-menu ul li {
  list-style-type: none;
}

.header-menu ul li {
  margin-right: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header-menu ul li:hover, .header-menu ul li:focus {
  transform: scale(1.3);
}

.header-menu ul li:not(:last-child) {
  margin-right: 40px;
}

.header-menu ul li a {
  color: #5c5c5c;
  text-decoration: none;
}

.header-menu ul li ul {
  width: 250px;
  visibility: hidden;
  opacity: 0;
  flex-wrap: wrap;
  transition: opacity .6s ease-in;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.header-menu ul li:hover ul {
  visibility: visible;
  opacity: 1;
}

.header-menu ul li ul li {
  width: 230px;
  background-color: #fff;
  padding: 5px 0 5px 20px;
  font-size: 14px;
  transition: none;
}

.header-menu ul li ul li:hover, .header-menu ul li ul li:focus {
  transform: scale(1);
}

.header-menu ul li .arrow-down {
  width: 0;
  height: 0;
  border-top: 5px solid #5c5c5c;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  transition-property: transform;
  transition-duration: .8s;
  position: absolute;
  top: 7px;
  right: -15px;
}

.header-menu ul li:hover .arrow-down {
  transform: rotate(180deg);
}

.three, .mobile-menu {
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .three, .mobile-menu {
    display: block;
  }
}

.visible {
  transition: all .5s;
  opacity: 1 !important;
}

.mobile-menu {
  height: 100vh;
  width: 100%;
  opacity: 0;
  background-color: #111;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-menu .container-mobile .menu-container {
  width: 250px;
  height: 100vh;
  z-index: 99;
  background-color: #e17992;
  flex-direction: column;
  padding: 60px 0;
  transition: transform .35s cubic-bezier(.8, .4, .3, .9);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu ul {
  padding: 50px 30px;
}

.mobile-menu ul li {
  margin: 15px 0;
  list-style-type: none;
  position: relative;
}

.mobile-menu ul li a {
  color: #efefef;
  font-size: 22px;
  text-decoration: none;
}

.mobile-menu ul li ul {
  margin: 0;
  padding: 0 30px;
}

.mobile-menu ul li div.click {
  color: #fff;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 220px;
}

.container-mobile ul ul li a {
  font-size: 18px;
}

.mobile-menu ul ul {
  display: none;
}

.hamburger {
  z-index: 1001;
  background-color: #111;
  padding: 5px;
  position: fixed;
  top: 20px;
  right: 20px;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger .line {
  width: 30px;
  height: 5px;
  -o-transition: all .3s ease-in-out;
  background-color: #fff;
  margin: 5px;
  transition: all .3s ease-in-out;
  display: block;
}

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  transform: translateY(10px)rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  transform: translateY(-10px)rotate(-45deg);
}

